.download {
  width: 100%;
  background-image: url(../img/download-image.png);
  min-height: 409px;
  padding-top: 90px;
}

.download__title {
  font-weight: 300;
  font-size: 36px;
  line-height: 145%;
  text-transform: uppercase;
  color: @white;
  margin-left: 760px;
}

.download__block {
  width: 665px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  margin-top: 55px;
}

.download__link {
  font-weight: 300;
  font-size: 16px;
  line-height: 20px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  text-decoration: none;
  text-align: center;
  color: @black;
  background-color: @white;
  padding-top: 25px;
  padding-bottom: 20px;
  padding-left: 13px;
  padding-right: 13px;
  &:hover {
    background-color: fade(@white, 70%);
  }
  &:active {
    background-color: fade(@white, 50%);
    color: fade(@black, 70%);
  }
}

.download__link:first-child {
  width: 234px;
}

.download__link:nth-child(2) {
  width: 294px;
}