.header {
  width: 1400px;
  min-height: 207px;
  margin: 0 auto;
  display: flex;
  align-items: center;
}

.header__list {
  width: 987px;
  min-height: 29px;
  margin-left: 100px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
}

.header__items {
  margin-right: 30px;
}

.header__link {
  text-decoration: none;
  padding: 7px;
  color: @black;
  font-size: 17px;
  text-transform: uppercase;
  line-height: 16px;
  &:hover {
    color: @dark-yellow;
  }
  &:active {
    color: fade(@dark-yellow, 50%);
  }
}

.header__link--active {
  color: fade(@dark-yellow, 70%);
  border: 1px solid fade(@dark-yellow, 70%);
}

.header__button {
  background-color: transparent;
  border: none;
  cursor: pointer;
}