.instagram {
  width: 100%;
  background-color: @background;
  padding-top: 120px;
  margin-top: 120px;
}

.instagram__block {
  width: 350px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 90px;
}

.instagram__title {
  font-weight: 300;
  font-size: 36px;
  line-height: 145%;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: @grey-title;
  margin-bottom: 5px;
}

.instagram__p {
  font-weight: 300;
  font-size: 9px;
  line-height: 11px;
  text-align: center;
  letter-spacing: 0.03em;
  text-transform: uppercase;
}

.instagram__list {
  display: flex;
}