.services {
  width: 1800px;
  margin: 0 auto;
  margin-bottom: 120px;
}

.services__list {
  display: flex;
  justify-content: space-between;
}

.services__items {
  position: relative;
}

.services__block {
  position: absolute;
  bottom: 0;
  width: 580px;
  background-color: fade(@dark-yellow, 80%);
  color: @white;
  padding-top: 21px;
  padding-bottom: 22px;
  text-align: center;
}

.services__border {
  width: 550px;
  min-height: 126px;
  border: 2px solid fade(@white, 50%);
  margin: 0 auto;
  padding-bottom: 20px;
}

.services__title {
  font-size: 30px;
  font-weight: 300;
  margin-bottom: 14px;

}

.services__p {
  font-size: 14px;
  width: 200px;
  margin: 0 auto;
  line-height: 135%;
}