.gallery {
  width: 1110px;
  margin: 0 auto;
  margin-bottom: 150px;
}

.gallery__list {
  display: flex;
  flex-basis: 960px;
  justify-content: flex-end;
  flex-wrap: wrap;
}

.gallery__items {
  margin-right: 30px;
  margin-bottom: 30px;
  height: 300px;
}

.gallery__items:nth-child(3),
.gallery__items:nth-child(5),
.gallery__items:nth-child(6) {
  margin-right: 0;
}

.gallery__items:nth-child(4),
.gallery__items:nth-child(6) {
  margin-bottom: 0;
}

.gallery__items:first-child {
  width: 255px;
  height: auto;
  text-align: center;
  padding: 40px;
  padding-top: 80px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.gallery__title {
  font-weight: 300;
  font-size: 24px;
  line-height: 135%;
  text-transform: uppercase;
  margin-bottom: 15px;
}

.gallery__p {
  font-weight: 300;
  font-size: 9px;
  line-height: 11px;
  text-transform: uppercase;
}