.contacts {
  width: 1110px;
  margin: 0 auto;
  display: flex;
  margin-top: 120px;
}

.contacts__form {
  width: 540px;
  display: flex;
  flex-direction: column;
}

.contacts__block {
  width: 529px;
}

.contacts__block-flex {
  display: flex;
}

.contacts__list {
  width: 255px;
}

.contacts__items {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 25px;
}

.contacts__items:last-child,
.contacts__items:first-child {
  margin-bottom: 0;
}

.contacts__p-inner {
  font-weight: 300;
  font-size: 16px;
  line-height: 135%;
  text-transform: uppercase;
}

.contacts__link {
  width: 200px;
  font-weight: 300;
  font-size: 14px;
  line-height: 135%;
  text-decoration: none;
  color: @black;
}

.contacts__link--map {
  text-decoration: underline;
}

.contacts__title {
  text-transform: uppercase;
  font-weight: 300;
  font-size: 36px;
  line-height: 145%;
  margin-top: 0;
}

.contacts__p {
  font-weight: 300;
  font-size: 14px;
  line-height: 135%;
  color: @text-grey;
}

//Ниже Форма

.contacts__input {
  padding: 12px;
  border: none;
  border-bottom: 1px solid @border-grey;
  margin-bottom: 15px;
}

.contacts__input--last {
  margin-bottom: 35px;
}

.contacts__input[placeholder] {
  font-weight: 300;
  font-size: 14px;
  line-height: 135%;
}

.contacts__text {
  padding: 12px;
  border: 1px solid @border-grey;
  margin-bottom: 25px;
  color: @text-grey;
  font-weight: 300;
  font-size: 14px;
  line-height: 135%;
  height: 160px;
}

.contacts__button {
  width: 170px;
  padding-left: 8px;
  padding-right: 8px;
  padding-top: 13px;
  padding-bottom: 13px;
  border: none;
  background-color: @dark-yellow;
  cursor: pointer;
  color: @white;
  font-weight: 300;
  font-size: 16px;
  line-height: 135%;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  &:hover {
    background-color: fade(@dark-yellow, 70%);
  }
  &:active {
    background-color: fade(@dark-yellow, 50%);
    color: fade(@white, 70%);
  }
}