body {
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  margin: 0;
  padding: 0;
  width: auto;
  background-color: @background;
}

.wrapper {
  width: 1920px;
  margin: 0 auto;
  background-color: @white;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

a {
  display: block;
}

img {
  display: block;
}