.info {
  margin-bottom: 120px;
  background: linear-gradient(to top, @background 63%, @white 40%);
  padding-bottom: 45px;
}

.info__block-inner {
  width: 287px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.info__title-big {
  font-size: 36px;
  line-height: 145%;
  text-transform: uppercase;
  font-weight: 300;
  margin-bottom: 0;
}

.info__p-min {
  font-size: 9px;
  text-transform: uppercase;
}

.info__link {
  width: 940px;
  margin: 0 auto;
}

.info__p {
  font-size: 14px;
  line-height: 135%;
  color: @text-grey;
}

.info__list {
  width: 1015px;
  margin-left: 505px;
  margin-top: 90px;
  display: grid;
  grid-template-rows: auto auto;
  grid-template-columns: 370px 250px 260px;
  column-gap: 30px;
  row-gap: 23px;
}

.info__items:first-child {
  grid-row-start: 1;
  grid-row-end: 3;
  width: 340px;
}

.info__img-icon {
  width: 203px;
  margin: 0 auto;
  margin-bottom: 33px;
}

.info__title {
  font-weight: 300;
  font-size: 16px;
  line-height: 135%;
  text-transform: uppercase;
  margin-left: 22px;
  margin-bottom: 0;
}

.info__inner {
  display: flex;
  align-items: center;
}