.footer {
  background-color: @background;
  width: 100%;
  padding-left: 405px;
  padding-right: 405px;
  padding-top: 40px;
  padding-bottom: 37px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer__list {
  display: flex;
}

.footer__items:nth-child(2) {
  margin-right: 5px;
  margin-left: 5px;
}

.footer__p {
  font-weight: 300;
  font-size: 14px;
  line-height: 135%;
}

.footer__link {
  font-weight: 300;
  font-size: 14px;
  line-height: 135%;
  text-decoration: none;
  color: @black;
  &:hover {
    text-decoration: underline;
  }
}